import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiLoadService } from '../api-load.service';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

  public estadoButton = true;
  public box;

  constructor(private apiLink: ApiLoadService, private rutaId: ActivatedRoute) { }

  ngOnInit() {
    var link = this.rutaId.snapshot['_routerState'].url;

    switch(link) {
      default:
        this.apiLink.getPokemons(); break;
        
      case '/pokemons':
        this.apiLink.getPokemons(); break;

      case '/trainers':
        this.apiLink.getTrainers(); break;

      case '/energies':
        this.apiLink.getEnergies(); break;
    }
  }

  cambiarColor() {
    if(this.estadoButton == false){
      this.estadoButton = true;
      this.box = {
        backgroundColor: '#222222'
      }
    }else{
      this.estadoButton = false;
      this.box = {
        backgroundColor: 'white'
      }
    }
  }
}
